import AddMaterialButton from '../../Materials/components/AddMaterialButton';
import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import OrderImage from './OderImage';
import SelectCustom from '../../../components/common/SelectCustom';

const MakeOrderMakeOrder = () => {
  const [product, setProduct] = useState({
   type: ''
  });

  const [productTypesAll, setProductTypesAll] = useState({
    ten: 10,
    twenty: 20,
    thirty: 30,
  });

  const classes = useStyles();

  const onChangeProductType = (event) => {
    const name = event.target.name;
    setProduct({
      ...product,
      [name]: event.target.value,
    });
  };

  return (
      <div className={classes.makeClientCreatedBlock}>
        <div className={classes.makeClientCreatedBlocklineOne}>
          <OrderImage/>
          <SelectCustom optionsObject={productTypesAll} label='Вид'
                        onChangeFunction={onChangeProductType} val={product.type}/>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeClientCreatedBlock: {
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '3%',
    paddingTop: '0',
    flexWrap: 'wrap',
    height: 'fit-content',
    marginTop: '20px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  makeClientCreatedBlocklineOne: {
    display: 'flex',
    marginTop: '10px',
  },
}));

export default MakeOrderMakeOrder;
